html {
  font-size: 24px;
}

.full-screen {
  min-height: 100vh;
}

.square {
  width: 4rem;
  height: 4rem;
  font-size: 2.5rem;
  text-align: center;
}

.v {
  border-left:1px solid #000;
  border-right:1px solid #000;
}

.h {
  border-top:1px solid #000;
  border-bottom:1px solid #000;
}

.tiny-square {
  width: 1.2rem;
  height: 1.2rem;
  font-size: .75rem;
  text-align: center;
}

.card-link {
  position: absolute;
  top: 0; left: 0;
  height: 100%; width: 100%;
}


